import React, { Component } from 'react'
import {connect} from 'react-redux'
import logo from '../mini.svg'
import '../css/Bar.css'

class Bar extends Component {
  constructor(){
    super()
    this.state = {x : -100}
  }
  render(){
    return(
    <div className='bar'>
      <img ref='minilogo' style={{'transform':'translateY('+this.state.x+'%) scale(1)'}} src={logo} className="App-mini" alt="minilogo" />
    </div>
  )
  }
}

export default connect(
state => ({ scroll : state.scroll }),
dispatch => ({})

)(Bar)



